<template>
  <v-dialog
      v-model="dialog"
      scrollable
      :persistent="dirty"
      width="740"
      @click:outside="dirty || close(false)">
    <v-card>
      <v-card-title>
        <span>Configuration</span>
        <v-spacer/>
        <v-btn icon @click="clickHelp">
          <v-icon>mdi-help-circle</v-icon>
        </v-btn>
      </v-card-title>
      <v-divider/>
      <v-card-text style="height: 700px;">
        <v-container>
          <v-row no-gutters>
            <v-col cols="3" sm="3" md="3">
              <v-subheader dence>カスタムグリッド幅</v-subheader>
            </v-col>
            <v-col cols="9" sm="9" md="9">
              <v-slider
                  v-model="config.customGridSize"
                  dense
                  min="5"
                  max="128"
                  class="align-center">
                <template v-slot:append>
                  <v-text-field
                      v-model="config.customGridSize"
                      dense
                      type="number"
                      style="width: 64px;">
                  </v-text-field>
                </template>
              </v-slider>
            </v-col>
            <v-col cols="12" sm="12" md="12">
              <v-divider class="pb-4"/>
            </v-col>
            <template v-for="blockId in Object.keys(config.blocks)">
              <v-col :key="blockId" cols="12" sm="12" md="12">
                <v-text-field
                    v-model="config.blocks[blockId]"
                    outlined
                    dense
                    :label="'ブロックID:' + blockId"/>
              </v-col>
            </template>
          </v-row>
        </v-container>
      </v-card-text>
      <v-divider/>
      <v-card-actions>
        <v-spacer/>
        <v-btn
            color="green darken-1"
            text
            @click="close(false)">
          Cancel
        </v-btn>
        <v-btn
            color="green darken-1"
            text
            :disabled="!dirty"
            @click="close(true)">
          Ok
        </v-btn>
      </v-card-actions>
    </v-card>
    <ConfigHelpDialog
        :dialog="helpDialog"
        @close="helpDialog = $event"/>
  </v-dialog>
</template>

<script>
import ConfigHelpDialog from "@/components/ConfigHelpDialog"

export default {
  components: {
    ConfigHelpDialog,
  },
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      config: null,
      dirty: false,
      helpDialog: false,
    }
  },
  watch: {
    dialog() {
      if(this.dialog) {
        this.init()
      }
    },
    config: {
      handler(val, oldVal) {
        if(this.dirty || val !== oldVal) { return }

        this.dirty = true
      },
      deep: true,
    },
  },
  methods: {
    init() {
      this.config = Object.assign({}, this.$store.state.config)
      this.config.blocks = Object.assign({}, this.$store.state.config.blocks)
      this.dirty = false
    },
    clickHelp() {
      this.helpDialog = true
    },
    close(isOk) {
      const result = isOk ? this.config : null
      this.$emit("close", result)
    },
  },
  created() {
    this.init()
  },
}
</script>

