<template>
  <v-row justify="center">
    <v-dialog
        v-model="dialog"
        persistent
        width="1100"
        max-width="1100"
    >
      <v-card>
        <v-card-title>
          <span>画像の読み込み</span>
          <v-spacer/>
          <v-btn icon @click="clickHelp">
            <v-icon>mdi-help-circle</v-icon>
          </v-btn>
        </v-card-title>

        <v-divider/>

        <v-container fluid>
          <v-row no-gutters>
            <v-col cols="2" sm="2" md="2">
              <v-select
                  label="構造"
                  v-model="selectConstruct.default"
                  :items="selectConstruct.items"
                  dense
                  outlined
                  @change="brightnesses = Array.from($event)"/>
            </v-col>
            <v-col cols="2" sm="2" md="2" offset="4" offset-sm="4" offset-md="4">
              <v-select
                  label="ブロックの種類"
                  v-model="selectBlock.default"
                  :items="selectBlock.items"
                  dense
                  outlined
                  @change="blocks = Array.from($event)"/>
            </v-col>

            <v-col cols="12" sm="12" md="12">
              <v-tooltip
                  v-model="blockSampleTooltip"
                  top>
                <span v-html="blockSamples[blockTooltipId]"></span>
                <template v-slot:activator="{ on }">
                  <canvas
                      ref="canvas"
                      width="1076"
                      height="120"
                      @mousedown="mouseDown"
                      @mousemove="mouseOver"
                      v-on="on"/>
                </template>
              </v-tooltip>
            </v-col>

            <v-row>
              <v-spacer/>
              <v-col cols="auto" sm="auto" md="auto">
                <v-list>
                  <v-list-item>
                    <PreviewCanvas :image-bitmap="readingImageBitmap"/>
                  </v-list-item>
                  <v-list-item>
                    <v-subheader>{{ warnImageSize }}</v-subheader>
                  </v-list-item>
                </v-list>
              </v-col>
              <v-spacer/>
              <v-col cols="4" sm="4" md="4" align-self="center">
                <v-card flat>
                  <v-card-text align="center">
                    <v-select
                        label="色の変換方法"
                        v-model="selectColorDiffMethod.default"
                        :items="selectColorDiffMethod.items"
                        dense
                        outlined
                        @change="colorDiffMethod = $event"/>
                    <v-icon x-large>mdi-arrow-right-bold</v-icon>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer/>
                    <v-btn
                        width="150"
                        :loading="converting"
                        :disabled="convertDisabled"
                        @click="clickConvert">
                      Convert
                    </v-btn>
                    <v-spacer/>
                  </v-card-actions>
                </v-card>
              </v-col>
              <v-spacer/>
              <v-col cols="auto" sm="auto" md="auto">
                <PreviewCanvas :image-bitmap="convertingImageBitmap"/>
              </v-col>
              <v-spacer/>
            </v-row>
          </v-row>
        </v-container>

        <v-card-actions>
          <v-spacer/>

          <v-btn
              color="green darken-1"
              text
              @click="clickCancel">
            Cancel
          </v-btn>

          <v-btn
              color="green darken-1"
              text
              :disabled="!convertingImageBitmap"
              @click="clickOk">
            Ok
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <ConvertImageHelpDialog
        :dialog="helpDialog"
        @close="helpDialog = $event"/>
    <LoadImageFile
        :image-src="imageSrc"
        @loaded="setReadingImageData"/>
  </v-row>
</template>

<script>
import { Color } from "@/models/Color"
import { Block } from "@/models/Block"

import checkboxOnSvg from "@/assets/check_box-24px.png"
import checkboxOffSvg from "@/assets/check_box_outline_blank-24px.png"
import PreviewCanvas from "@/components/PreviewCanvas"
import LoadImageFile from "@/components/LoadImageFile"
import ConvertImageHelpDialog from "@/components/ConvertImageHelpDialog";

export default {
  components: {
    ConvertImageHelpDialog,
    PreviewCanvas,
    LoadImageFile,
  },
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
    imageSrc: {
      type: File,
      default: null,
    },
  },
  data() {
    return {
      converting: false,
      convertDisabled: false,
      selectBlock: {
        default: {
          text: "ベテラン",
          value: [true, true, false, true, true, true, true, true, false, true, true, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, false, true, false, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true],
        },
        items: [
          { text: "ビギナー",
            value: [true, true, false, false, false, false, true, true, false, true, true, false, true, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, false, false, false, false, true, true, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false] },
          { text: "ベテラン",
            value: [true, true, false, true, true, true, true, true, false, true, true, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, false, true, false, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true] },
          { text: "全て",
            value: [true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true] },
        ],
      },
      selectConstruct: {
        default: {
          text: "平たん",
          value: [false, true, false],
        },
        items: [
          { text: "平たん",
            value: [false, true, false] },
          { text: "立体",
            value: [true, true, true] },
        ],
      },
      selectColorDiffMethod: {
        default: {
          text: "ユークリッド（簡易、軽い）",
          value: "euclid",
        },
        items: [
          { text: "ユークリッド（簡易、軽い）",
            value: "euclid" },
          { text: "Cie76（高品質、重い）",
            value: "cie76" },
        ],
      },
      blocks: [true, true, false, true, true, true, true, true, false, true, true, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, false, true, false, false, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true],
      brightnesses: [false, true, false],
      colorDiffMethod: "euclid",

      blockSampleTooltip: false,
      blockTooltipId: null,

      readingImageData: null,
      readingImageBitmap: null,
      convertingImageData: null,
      convertingImageBitmap: null,
      warnImageSize: "",
      checkboxOnImage: null,
      checkboxOffImage: null,

      helpDialog: false,
    }
  },
  computed: {
    paletteImageBitmap() {
      return this.$store.state.colorPaletteImageBitmap
    },
    blockSamples() {
      let samples = []
      Object.keys(Block.SAMPLES).forEach((id) => {
        samples[id] = `ブロックID: ${id}<br>${Block.SAMPLES[id]}`
      })
      return samples
    },
  },
  methods: {
    draw() {
      const ctx = this.ctx
      if(ctx === undefined) { return }

      ctx.clearRect(0, 0, 1076, 120)

      ctx.drawImage(this.paletteImageBitmap, 35, 50, this.paletteImageBitmap.width * 20, this.paletteImageBitmap.height * 20)

      ctx.strokeStyle = "gray"
      ctx.lineWidth = 1
      ctx.beginPath()
      ctx.moveTo(12, 0)
      ctx.lineTo(12, 45)
      ctx.moveTo(45, 20)
      ctx.lineTo(45, 1)
      ctx.lineTo(1045, 1)
      ctx.lineTo(1045, 20)
      ctx.stroke()

      this.brightnesses.forEach((block, i) => {
        ctx.drawImage(block ? this.checkboxOnImage : this.checkboxOffImage, 1, i * 20 + 49, 22, 22)
      })
      this.blocks.forEach((brightness, i) => {
        ctx.drawImage(brightness ? this.checkboxOnImage : this.checkboxOffImage, i * 20 + 34, 26, 22, 22)
      })
    },
    async setReadingImageData(imageData) {
      this.readingImageData = imageData
      this.readingImageBitmap = await createImageBitmap(imageData)
      if(imageData.width > 1024 || imageData.height > 1024) {
        this.warnImageSize = "※ 1024px以上は読み込み出来ません"
      } else {
        this.warnImageSize = ""
      }
    },
    mouseDown(e) {
      if (1 < e.layerX && e.layerX < 22 &&
          49 < e.layerY && e.layerY < this.brightnesses.length * 20 + 49) {
        const index = Math.floor((e.layerY - 49) / 20)
        if(index < 0 || index >= this.brightnesses.length) { return }

        this.brightnesses[index] = !this.brightnesses[index]
      }
      else if (34 < e.layerX && e.layerX < this.blocks.length * 20 + 34 &&
          26 < e.layerY && e.layerY < 48) {
        const index = Math.floor((e.layerX - 34) / 20)
        if(index < 0 || index >= this.blocks.length) { return }

        this.blocks[index] = !this.blocks[index]
      }
      this.convertDisabled = !(this.brightnesses.includes(true) && this.blocks.includes(true))
      this.draw()
    },
    mouseOver(e) {
      if (34 < e.layerX && e.layerX < this.blocks.length * 20 + 34 &&
          26 < e.layerY && e.layerY < 48) {
        const index = Math.floor((e.layerX - 34) / 20)
        if(index < 0 || index >= this.blocks.length) { return }

        this.blockSampleTooltip = true
        this.blockTooltipId = index + 1
      } else {
        this.blockSampleTooltip = false
        this.blockTooltipId = null
      }
    },
    async clickConvert() {
      this.converting = true

      const self = this
      window.requestIdleCallback(() => async function() {
        const colorConverter = new Color(self.blocks, self.brightnesses)
        let method
        switch(self.colorDiffMethod) {
          case "euclid":
            method = Color.euclid
            break
          case "cie76":
            method = Color.cie76
            break
          default:
            method = Color.euclid
        }

        self.convertingImageData = await colorConverter.convertImageData(self.readingImageData, method)
        self.convertingImageBitmap = await createImageBitmap(self.convertingImageData)
        self.converting = false
      }())
    },
    clickOk() {
      this.$store.commit("setImageData", this.convertingImageData)
      this.$store.commit("setImageBitmap", this.convertingImageBitmap)
      this.$store.commit("setOriginImageBitmap", this.readingImageBitmap)
      this.resetImages()
      this.close()
    },
    clickCancel() {
      this.resetImages()
      this.close()
    },
    clickHelp() {
      this.helpDialog = true
    },
    resetImages() {
      this.readingImageData = null
      this.readingImageBitmap = null
      this.convertingImageData = null
      this.convertingImageBitmap = null
    },
    close() {
      this.$emit('close')
    },
  },
  mounted() {
    if(this.$refs.canvas !== undefined) {
      this.ctx = this.$refs.canvas.getContext('2d')
      this.ctx.imageSmoothingEnabled = false
    }

    this.checkboxOnImage = new Image()
    this.checkboxOnImage.src = checkboxOnSvg
    this.checkboxOffImage = new Image()
    this.checkboxOffImage.src = checkboxOffSvg

    this.draw()
  },
  updated() {
    if(this.$refs.canvas !== undefined) {
      this.ctx = this.$refs.canvas.getContext('2d')
      this.ctx.imageSmoothingEnabled = false
    }
    this.draw()
  },
}
</script>

<style scoped>
</style>
