<template>
  <v-row justify="center">
    <v-dialog
        v-model="dialog"
        width="1140"
        height="420"
        @click:outside="close">
      <v-card>
        <v-container fluid>
          <v-row>
            <v-col align="center" cols="4" sm="4" md="4">
              <PreviewCanvas :image-bitmap="imageBitmap"/>
              <canvas ref="canvas" hidden :width="width" :height="height"/>
              <v-btn @click="downloadPng">画像をダウンロード</v-btn>
            </v-col>
            <v-col align="center" cols="7" sm="7" md="7">
              <v-textarea
                  label="ブロック数"
                  no-resize
                  rows="8"
                  v-model="text"/>
              <v-btn @click="downloadText">テキストファイルをダウンロード</v-btn>
            </v-col>
            <v-col v-if="debug" align="center" cols="1" sm="1" md="1">
              <v-btn icon elevation="2" @click="downloadFunction">
                <v-icon>mdi-function</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { Color } from "@/models/Color"
import { Block } from "@/models/Block"

import PreviewCanvas from "@/components/PreviewCanvas"

import JSZip from 'jszip'

export default {
  components: {PreviewCanvas},
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
    imageData: {
      type: ImageData,
      default: null,
    },
    imageBitmap: {
      type: ImageBitmap,
      default: null,
    },
  },
  data() {
    return {
      text: "",
      debug: false,
    }
  },
  computed: {
    width() {
      if(!this.imageData) { return 0 }
      return this.imageData.width
    },
    height() {
      if(!this.imageData) { return 0 }
      return this.imageData.height
    },
  },
  watch: {
    dialog() {
      if(this.dialog) {
        let blockCount = []
        for(let y = 0; y < this.height; y++) {
          for(let x = 0; x < this.width; x++) {
            const i = (x + y * this.width) * 4
            const [r, g, b] = this.imageData.data.slice(i, i + 3)
            const blockId = Color.getBlock(r, g, b).blockId
            if(!blockCount[blockId - 1]) { blockCount[blockId - 1] = { id: blockId, count: 0 } }
            blockCount[blockId - 1].count += 1
          }
        }
        blockCount.sort((a, b) => {
          if(a.count < b.count) { return 1 }
          return -1
        })

        this.text = ""
        blockCount.forEach((b) => {
          if(b) { this.text += `ID#${b.id}: ${this.$store.state.config.blocks[b.id]} => ${b.count}\n` }
        })
      }
    },
  },
  methods: {
    draw() {
      if(!this.imageBitmap) { return }

      this.ctx.clearRect(0, 0, this.width, this.height)
      this.ctx.drawImage(this.imageBitmap, 0, 0)
    },
    downloadPng() {
      const dataUrl = this.$refs.canvas.toDataURL('image/png')
      const bin = atob(dataUrl.split(',')[1])
      const buffer = new Uint8Array(bin.length)
      for (let i = 0; i < bin.length; i++) {
        buffer[i] = bin.charCodeAt(i)
      }
      const blob = new Blob([buffer.buffer], {type: 'image/png'})

      const link = document.createElement('a')
      link.download = "converted.png"
      link.href = URL.createObjectURL(blob)
      link.click()
      URL.revokeObjectURL(link.href)
    },
    downloadText() {
      const blob = new Blob([this.text], {type: 'text/plain'})
      const link = document.createElement('a')
      link.download = "blocks.txt"
      link.href = URL.createObjectURL(blob)
      link.click()
      URL.revokeObjectURL(link.href)
    },
    async downloadFunction() {
      const zip = new JSZip()
      zip.folder("map_illustrator").folder("data").folder("map_illustrator").folder("functions")
      zip.file("map_illustrator/pack.mcmeta", '{"pack": {"pack_format": 1, "description": "datapack"}}')

      let commands = `fill -64 128 -65 ${-64 + this.width} 128 -65 minecraft:stone\n`
      let my
      let flag = 0
      let count = 1
      let functions = 1
      for(let x = 0; x < this.width; x++) {
        my = 128
        for(let y = 0; y < this.height; y++) {
          const i = (x + y * this.width) * 4
          const [r, g, b] = this.$store.state.image.imageData.data.slice(i, i + 3)
          const block = Color.getBlock(r, g, b)

          if(block.brightnessIdx === 0) {
            if(flag === 2) {
              flag = 0
              my = 128
            } else {
              my += 1
            }
          }
          else if(block.brightnessIdx === 2) {
            if(flag === 0) {
              flag = 2
              my = 128
            } else {
              my -= 1
            }
          }
          if(my <= 0 || my >= 255) { console.log(`err: [${x} ${my} ${y}]`) }
          commands += `setblock ${x - 64} ${my} ${y - 64} ${Block.SET_BLOCKS[block.blockId]}\n`

          count += 1
          if(count >= 65355) {
            zip.file(`map_illustrator/data/map_illustrator/functions/fill${functions}.mcfunction`, commands)
            count = 0
            commands = ""
            functions += 1
          }
        }
      }
      if(count > 0) {
        zip.file(`map_illustrator/data/map_illustrator/functions/fill${functions}.mcfunction`, commands)
      }

      const blob = await zip.generateAsync({ type: 'blob' })
      const link = document.createElement('a')
      link.download = "minecraft_map_illust_function.zip"
      link.href = URL.createObjectURL(blob)
      link.click()
      URL.revokeObjectURL(link.href)
    },
    close() {
      this.$emit('close')
    },
  },
  mounted() {
    if(this.$refs.canvas) {
      this.ctx = this.$refs.canvas.getContext('2d')
      this.ctx.imageSmoothingEnabled = false
      this.draw()
    }
  },
  updated() {
    if (this.$refs.canvas) {
      this.ctx = this.$refs.canvas.getContext('2d')
      this.ctx.imageSmoothingEnabled = false
      this.draw()
    }
  },
}
</script>

