import Vue from 'vue'
import Vuex from 'vuex'
import {Color} from "@/models/Color";
import {Block} from "@/models/Block";

Vue.use(Vuex)

function setCurrentColor(state, x, y) {
  const i = (x + y * state.image.imageData.width) * 4

  state.image.imageData.data[i] = state.currentColor.r
  state.image.imageData.data[i + 1] = state.currentColor.g
  state.image.imageData.data[i + 2] = state.currentColor.b
}

export default new Vuex.Store({
  state: {
    image: {
      imageData: null,
      imageBitmap: null,
    },
    originImage: {
      imageBitmap: null,
    },
    colorPaletteImageBitmap: null,
    currentColor: null,
    drawCellSize: 4,
    drawOriginX: 0,
    drawOriginY: 0,
    cursorX: null,
    cursorY: null,
    cursorMode: "move",
    spaceKey: false,
    config: {
      customGridSize: 16,
      blocks: Object.assign({}, Block.SAMPLES),
    },
  },
  getters: {
    imageWidth(state) {
      if(!state.image.imageData) { return 0 }
      return state.image.imageData.width
    },
    imageHeight(state) {
      if(!state.image.imageData) { return 0 }
      return state.image.imageData.height
    },
    currentBlock: (state, getters) => {
      if(state.cursorX !== null && state.cursorY !== null && state.image.imageData !== null) {
        const i = (state.cursorX + state.cursorY * getters.imageWidth) * 4
        const [r, g, b] = state.image.imageData.data.slice(i, i + 3)
        return Color.getBlock(r, g, b)
      } else {
        return null
      }
    },
    currentBlockId: (state, getters) => {
      const block = getters.currentBlock
      return block ? block.blockId : ""
    },
    currentBlockName: (state, getters) => {
      const blockId = getters.currentBlockId
      return blockId ? state.config.blocks[blockId] : ""
    },
    currentBrightnessName: (state, getters) => {
      const block = getters.currentBlock
      if(!block) { return "" }

      switch(block.brightnessIdx) {
        case 0:
          return "明るい"
        case 1:
          return "平たん"
        case 2:
          return "暗い"
      }
      return ""
    },
  },
  mutations: {
    setSpaceKey(state, flag) {
      state.spaceKey = flag
    },
    setCursorMode(state, mode) {
      state.cursorMode = mode
    },
    setColorPaletteImageBitmap(state, imageBitmap) {
      state.colorPaletteImageBitmap = imageBitmap
    },
    // setNewImage(state, {width, height}) {
    //   state.image.imageData = new ImageData(width, height)
    //   const color = Color.getColor(1, 1)
    //   for(let y = 0; y < height; y++) {
    //     for(let x = 0; x < width; x++) {
    //       const i = (x + y * width) * 4
    //       state.image.imageData.data[i] = color.r
    //       state.image.imageData.data[i + 1] = color.g
    //       state.image.imageData.data[i + 2] = color.b
    //       state.image.imageData.data[i + 3] = 255
    //     }
    //   }
    // },
    setImageData(state, imageData) {
      state.image.imageData = imageData
    },
    setImageBitmap(state, imageBitmap) {
      state.image.imageBitmap = imageBitmap
    },
    setOriginImageBitmap(state, imageBitmap) {
      state.originImage.imageBitmap = imageBitmap
    },
    setCurrentColor(state, {blockId, brightnessIdx}) {
      state.currentColor = Object.assign(Color.getColor(blockId, brightnessIdx), {blockId, brightnessIdx})
    },
    setDrawCellSize(state, cellSize) {
      state.drawCellSize = cellSize
    },
    setDrawOriginX(state, originX) {
      state.drawOriginX = originX
    },
    setDrawOriginY(state, originY) {
      state.drawOriginY = originY
    },
    setCursorX(state, cursorX) {
      state.cursorX = cursorX
    },
    setCursorY(state, cursorY) {
      state.cursorY = cursorY
    },
    setColor(state, {x, y}) {
      if(x === null || y === null) { return }

      setCurrentColor(state, x, y)
    },
    lineColor(state, {x1, y1, x2, y2}) {
      if(x1 === null || y1 === null || x2 === null || y2 === null) { return }

      // Bresenham's line algorithm
      const dx = Math.abs(x1 - x2);
      const dy = Math.abs(y1 - y2);
      const sx = (x1 < x2) ? 1 : -1;
      const sy = (y1 < y2) ? 1 : -1;
      let err = dx - dy;

      while (!((x1 === x2) && (y1 === y2))) {
        const e2 = err << 1;
        if (e2 > -dy) { err -= dy; x1 += sx; }
        if (e2 < dx) { err += dx; y1 += sy; }
        setCurrentColor(state, x1, y1)
      }
    },
    fillColor(state, {x, y}) {
      if(x === null || y === null) { return }

      let i = (x + y * state.image.imageData.width) * 4
      const targetColor = {
        r: state.image.imageData.data[i],
        g: state.image.imageData.data[i + 1],
        b: state.image.imageData.data[i + 2],
      }
      if(Color.equals(state.currentColor, targetColor)) { return }

      let targets = [{x, y}]
      let checked = []
      while(targets.length > 0) {
        const t = targets.pop()
        if(checked[t.y] === undefined) { checked[t.y] = [] }
        if(checked[t.y+1] === undefined) { checked[t.y+1] = [] }
        if(checked[t.y-1] === undefined) { checked[t.y-1] = [] }

        i = (t.x + t.y * state.image.imageData.width) * 4
        const checkColor = {
          r: state.image.imageData.data[i],
          g: state.image.imageData.data[i + 1],
          b: state.image.imageData.data[i + 2],
        }
        checked[t.y][t.x] = Color.equals(checkColor, targetColor)
        if(!checked[t.y][t.x]) { continue }

        if(t.x+1 < state.image.imageData.width
            && checked[t.y][t.x+1] === undefined) {
          targets.push({x: t.x+1, y: t.y})
        }
        if(t.x-1 >= 0
            && checked[t.y][t.x-1] === undefined) {
          targets.push({x: t.x-1, y: t.y})
        }
        if(t.y+1 < state.image.imageData.height
            && checked[t.y+1][t.x] === undefined) {
          targets.push({x: t.x, y: t.y+1})
        }
        if(t.y-1 >= 0
            && checked[t.y-1][t.x] === undefined) {
          targets.push({x: t.x, y: t.y-1})
        }
      }

      Object.keys(checked).forEach((cy) => {
        Object.keys(checked[cy]).forEach((cx) => {
          if(checked[cy][cx]) {
            setCurrentColor(state, Number(cx), Number(cy))
          }
        })
      })
    },
    setConfig(state, config) {
      state.config = config
    }
  },
  actions: {
    async calcColorPaletteImageBitmap(context) {
      const width = Object.keys(Color.PALETTE).length
      const height = Object.keys(Color.BRIGHTNESSES).length
      const imageData = new ImageData(width, height)
      Object.keys(Color.PALETTE).forEach((blockId, x) => {
        Object.keys(Color.BRIGHTNESSES).forEach((brightnessIdx, y) => {
          const color = Color.getColor(blockId, brightnessIdx)
          const i = (x + y * width) * 4
          imageData.data[i] = color.r
          imageData.data[i + 1] = color.g
          imageData.data[i + 2] = color.b
          imageData.data[i + 3] = 255
        })
      })

      const imageBitmap = await createImageBitmap(imageData)
      context.commit("setColorPaletteImageBitmap", imageBitmap)
    },
    async calcImageBitmap(context) {
      const imageBitmap = await createImageBitmap(context.state.image.imageData)
      context.commit("setImageBitmap", imageBitmap)
    },
    async loadOriginImage(context, imageData) {
      context.commit("setReadingImageData", imageData)

      const imageBitmap = await createImageBitmap(imageData)
      context.commit("setReadingImageBitmap", imageBitmap)
    },
  },
  modules: {
  }
})
