<template>
  <canvas ref="canvas" hidden width="1024" height="1024"/>
</template>

<script>
export default {
  props: {
    imageSrc: {
      type: File,
      default: null,
    },
  },
  data() {
    return {
      image: null,
    }
  },
  watch: {
    imageSrc() {
      this.load()
    }
  },
  methods: {
    load() {
      const fr = new FileReader()
      fr.readAsDataURL(this.imageSrc)
      fr.addEventListener('load', () => {
        this.image = new Image()
        this.image.onload = () => {
          this.draw()
        }
        this.image.src = fr.result
      })
    },
    draw() {
      this.ctx.clearRect(0, 0, 1024, 1024)
      this.ctx.drawImage(this.image, 0, 0)

      const imageData = this.ctx.getImageData(0, 0, this.image.width, this.image.height)
      this.$emit("loaded", imageData)
    },
  },
  mounted() {
    this.ctx = this.$el.getContext('2d')
    this.ctx.imageSmoothingEnabled = false
  }
}
</script>

