export class Color {
    static PALETTE = {
        1: [127, 178, 56],
        2: [247, 233, 163],
        3: [199, 199, 199],
        4: [255, 0, 0],
        5: [160, 160, 255],
        6: [167, 167, 167],
        7: [0, 124, 0],
        8: [255, 255, 255],
        9: [164, 168, 184],
        10: [151, 109, 77],
        11: [112, 112, 112],
        12: [64, 64, 255],
        13: [143, 119, 72],
        14: [255, 252, 245],
        15: [216, 127, 51],
        16: [178, 76, 216],
        17: [102, 153, 216],
        18: [229, 229, 51],
        19: [127, 204, 25],
        20: [242, 127, 165],
        21: [76, 76, 76],
        22: [153, 153, 153],
        23: [76, 127, 153],
        24: [127, 63, 178],
        25: [51, 76, 178],
        26: [102, 76, 51],
        27: [102, 127, 51],
        28: [153, 51, 51],
        29: [25, 25, 25],
        30: [250, 238, 77],
        31: [92, 219, 213],
        32: [74, 128, 255],
        33: [0, 217, 58],
        34: [129, 86, 49],
        35: [112, 2, 0],
        36: [209, 177, 161],
        37: [159, 82, 36],
        38: [149, 87, 108],
        39: [112, 108, 138],
        40: [186, 133, 36],
        41: [103, 117, 53],
        42: [160, 77, 78],
        43: [57, 41, 35],
        44: [135, 107, 98],
        45: [87, 92, 92],
        46: [122, 73, 88],
        47: [76, 62, 92],
        48: [76, 50, 35],
        49: [76, 82, 42],
        50: [142, 60, 46],
        51: [37, 22, 16],
    }
    static BRIGHTNESSES = {
        0: 255,
        1: 220,
        2: 180,
    }

    static COLOR_TO_BLOCK = []
    static BLOCK_TO_COLOR = new Map()

    static init() {
        Object.keys(this.PALETTE).forEach((blockId) => {
            blockId = Number(blockId)
            this.COLOR_TO_BLOCK[blockId] = []
            Object.keys(this.BRIGHTNESSES).forEach((brightnessIdx) => {
                brightnessIdx = Number(brightnessIdx)
                const r = Math.round(this.PALETTE[blockId][0] * this.BRIGHTNESSES[brightnessIdx] / 255)
                const g = Math.round(this.PALETTE[blockId][1] * this.BRIGHTNESSES[brightnessIdx] / 255)
                const b = Math.round(this.PALETTE[blockId][2] * this.BRIGHTNESSES[brightnessIdx] / 255)

                this.COLOR_TO_BLOCK[blockId][brightnessIdx] = {r, g, b}

                if(this.BLOCK_TO_COLOR[r] === undefined) { this.BLOCK_TO_COLOR[r] = new Map() }
                if(this.BLOCK_TO_COLOR[r][g] === undefined) { this.BLOCK_TO_COLOR[r][g] = new Map() }
                this.BLOCK_TO_COLOR[r][g][b] = {blockId, brightnessIdx}
            })
        })
    }

    static getColor(blockId, brightnessIdx) {
        return this.COLOR_TO_BLOCK[blockId][brightnessIdx]
    }

    static getBlock(r, g, b) {
        return this.BLOCK_TO_COLOR[r][g][b]
    }

    static equals(color1, color2) {
        return color1.r === color2.r && color1.g === color2.g && color1.b === color2.b
    }

    static euclid(x1, y1, z1, x2, y2, z2) {
        return Math.sqrt((x1 - x2) ** 2 + (y1 - y2) ** 2 + (z1 - z2) ** 2)
    }

    static cie76(r1, g1, b1, r2, g2, b2) {
        const [l1, a1, bb1] = Color.rgb2Lab(r1, g1, b1)
        const [l2, a2, bb2] = Color.rgb2Lab(r2, g2, b2)
        return Color.euclid(l1, a1, bb1, l2, a2, bb2)
    }

    static rgb2Lab(r, g, b) {
        const [x, y, z] = Color.rgb2xyz(r, g, b)
        return Color.xyz2Lab(x, y, z)
    }

    // https://en.wikipedia.org/wiki/SRGB#The_reverse_transformation
    static rgb2xyz(r, g, b) {
        r = r / 255.0
        g = g / 255.0
        b = b / 255.0

        r = r > 0.04045 ? ((r + 0.055) / 1.055) ** 2.4 : (r / 12.92)
        g = g > 0.04045 ? ((g + 0.055) / 1.055) ** 2.4 : (g / 12.92)
        b = b > 0.04045 ? ((b + 0.055) / 1.055) ** 2.4 : (b / 12.92)

        const x = (r * 0.4124) + (g * 0.3576) + (b * 0.1805)
        const y = (r * 0.2126) + (g * 0.7152) + (b * 0.0722)
        const z = (r * 0.0193) + (g * 0.1192) + (b * 0.9505)
        return [x, y, z]
    }

    // https://en.wikipedia.org/wiki/Lab_color_space#CIELAB-CIEXYZ_conversions
    static xyz2Lab(x, y, z) {
        x = x * 100 / 95.047
        z = z * 100 / 108.883

        x = x > 0.008856 ? x ** (1 / 3.0) : (7.787 * x) + (4 / 29.0)
        y = y > 0.008856 ? y ** (1 / 3.0) : (7.787 * y) + (4 / 29.0)
        z = z > 0.008856 ? z ** (1 / 3.0) : (7.787 * z) + (4 / 29.0)

        const l = (116 * y) - 16
        const a = 500 * (x - y)
        const b = 200 * (y - z)
        return [l, a, b]
    }

    enableColors = []
    constructor(enableBlockIds, enableBrightnessIdxes) {
        Object.keys(Color.COLOR_TO_BLOCK).forEach((blockId, x) => {
            Object.keys(Color.COLOR_TO_BLOCK[blockId]).forEach((brightnessIdx, y) => {
                if(enableBlockIds[x] && enableBrightnessIdxes[y]) {
                    this.enableColors.push(Color.COLOR_TO_BLOCK[blockId][brightnessIdx])
                }
            })
        })
    }

    async convertImageData(imageData, colorDiffMethod) {
        const width = Math.min(imageData.width, 1024)
        const height = Math.min(imageData.height, 1024)
        const convertedImageData = new ImageData(width, height)
        const colorCache = {}
        for(let y = 0; y < height; y++) {
            for(let x = 0; x < width; x++) {
                const originI = (x + y * imageData.width) * 4
                const [r, g, b] = imageData.data.slice(originI, originI + 3)

                let color
                if(colorCache[`${r},${g},${b}`]) {
                    color = colorCache[`${r},${g},${b}`]
                } else {
                    let minDist = 99999999
                    this.enableColors.forEach((paletteColor) => {
                        const dist = colorDiffMethod(r, g, b, paletteColor.r, paletteColor.g, paletteColor.b)
                        if (dist < minDist) {
                            minDist = dist
                            color = paletteColor
                        }
                    })
                    colorCache[`${r},${g},${b}`] = color
                }

                const i = (x + y * width) * 4
                convertedImageData.data[i] = color.r
                convertedImageData.data[i + 1] = color.g
                convertedImageData.data[i + 2] = color.b
                convertedImageData.data[i + 3] = 255
            }
        }
        return convertedImageData
    }
}
Color.init()
