<template>
  <v-dialog
      v-model="dialog"
      :width="slideWidth"
      @click:outside="close">
    <v-carousel
        v-model="slideIndex"
        :width="slideWidth"
        :height="slideHeight">
      <v-carousel-item
          v-for="(slide, i) in slides"
          :width="slideWidth"
          :height="slideHeight"
          :key="i"
          :src="slide">
      </v-carousel-item>
    </v-carousel>
  </v-dialog>
</template>

<script>
import slide1 from "@/assets/slide1.png"
import slide2 from "@/assets/slide2.png"
import slide3 from "@/assets/slide3.png"
import slide4 from "@/assets/slide4.png"
import slide5 from "@/assets/slide5.png"
import slide6 from "@/assets/slide6.png"
import slide7 from "@/assets/slide7.png"

export default {
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      slideIndex: 0,
      slides: [
        slide1,
        slide2,
        slide3,
        slide4,
        slide5,
        slide6,
        slide7,
      ],
      slideWidth: 864,
      slideHeight: 640,
    }
  },
  methods: {
    close() {
      this.slideIndex = 0
      this.$emit("close")
    },
  },
}
</script>

<style scoped>
</style>
