<template>
  <canvas
      width="1024"
      height="64"
      @mousedown="mouseDown"
  />
</template>

<script>
import { Color } from "@/models/Color"

export default {
  data() {
    return {
      canvasWidth: 1020,
      canvasHeight: 60,
      cellSize: 20,
    }
  },
  computed: {
    currentColorX() {
      if(!this.$store.state.currentColor) { return 0 }
      return this.$store.state.currentColor.blockId - 1
    },
    currentColorY() {
      if(!this.$store.state.currentColor) { return 0 }
      return this.$store.state.currentColor.brightnessIdx
    },
    colorPaletteImageBitmap() {
      return this.$store.state.colorPaletteImageBitmap
    },
  },
  watch: {
    currentColorX() { this.draw() },
    currentColorY() { this.draw() },
    colorPaletteImageBitmap() { this.draw() },
  },
  methods: {
    draw() {
      if(!this.colorPaletteImageBitmap) { return }

      const ctx = this.ctx
      ctx.clearRect(0, 0, this.canvasWidth + 4, this.canvasHeight + 4)

      const width = this.colorPaletteImageBitmap.width
      const height = this.colorPaletteImageBitmap.height
      ctx.drawImage(this.colorPaletteImageBitmap, 2, 2, width * this.cellSize, height * this.cellSize)

      ctx.strokeStyle = "black"
      ctx.lineWidth = 2
      ctx.strokeRect(
          this.currentColorX * this.cellSize + 1,
          this.currentColorY * this.cellSize + 1,
          this.cellSize + 2, this.cellSize + 2
      )
    },
    mouseDown(e) {
      const currentColorX = Math.min(Math.floor(e.layerX / this.cellSize), Object.keys(Color.PALETTE).length - 1)
      const currentColorY = Math.min(Math.floor(e.layerY / this.cellSize), Object.keys(Color.BRIGHTNESSES).length - 1)
      this.setCurrentColor(currentColorX, currentColorY)
    },
    setCurrentColor(currentColorX, currentColorY) {
      this.$store.commit("setCurrentColor", {blockId: currentColorX + 1, brightnessIdx: currentColorY})
    }
  },
  mounted() {
    this.ctx = this.$el.getContext('2d')
    this.ctx.imageSmoothingEnabled = false
    this.setCurrentColor(0, 0)
    this.draw()

    this.$store.dispatch("calcColorPaletteImageBitmap")
  },
  updated() {
    this.ctx.imageSmoothingEnabled = false
    this.draw()
  },
}
</script>

