<template>
  <canvas ref="canvas" width="256" height="256"/>
</template>

<script>
export default {
  props: {
    imageBitmap: {
      type: ImageBitmap,
      default: null,
    },
  },
  watch: {
    imageBitmap() {
      this.draw()
    }
  },
  methods: {
    draw() {
      this.ctx.clearRect(0, 0, 256, 256)

      if(!this.imageBitmap) { return }

      const width = this.imageBitmap.width
      const height = this.imageBitmap.height
      let newX = 0, newWidth = 256
      let newY = 0, newHeight = 256
      if(width > height) {
        newHeight = 256 * height / width
        newY = (256 - newHeight) / 2
      } else {
        newWidth = 256 * width / height
        newX = (256 - newWidth) / 2
      }
      this.ctx.drawImage(this.imageBitmap, newX, newY, newWidth, newHeight)
    },
  },
  mounted() {
    this.ctx = this.$el.getContext('2d')
    this.ctx.imageSmoothingEnabled = false
    this.draw()
  },
}
</script>

