<template>
  <v-dialog
      v-model="dialog"
      :width="slideWidth"
      @click:outside="close">
    <v-carousel
        v-model="slideIndex"
        :width="slideWidth"
        :height="slideHeight">
      <v-carousel-item
          v-for="(slide, i) in slides"
          :width="slideWidth"
          :height="slideHeight"
          :key="i"
          :src="slide">
      </v-carousel-item>
    </v-carousel>
  </v-dialog>
</template>

<script>
import slide4 from "@/assets/help_slide4.png"

export default {
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      slideIndex: 0,
      slides: [
        slide4,
      ],
      slideWidth: 1179,
      slideHeight: 770,
    }
  },
  methods: {
    close() {
      this.slideIndex = 0
      this.$emit("close")
    },
  },
}
</script>

