<template>
  <About/>
</template>

<script>
import About from '@/views/About.vue'

export default {
  name: 'App',
  components: {
    About,
  },
  methods: {
    onKeyDown(e) {
      if(this.$store.state.spaceKey || e.code !== "Space") { return }

      this.$store.commit("setSpaceKey", true)
    },
    onKeyUp(e) {
      if(!this.$store.state.spaceKey || e.code !== "Space") { return }

      this.$store.commit("setSpaceKey", false)
    }
  },
  mounted() {
    window.addEventListener("keydown", this.onKeyDown)
    window.addEventListener("keyup", this.onKeyUp)
  },
  destroyed() {
    window.removeEventListener("keydown", this.onKeyDown)
    window.removeEventListener("keyup", this.onKeyUp)
  }
}
</script>

