<template>
    <canvas
        :width="canvasWidth"
        :height="canvasHeight"
        @mousedown="mouseDown"
        @mousemove="mouseMove"
        @mouseup="mouseUp"
        @mousewheel="mouseWheel"
    />
</template>

<script>
import { Color } from '@/models/Color'

import brightnessIcon0 from '@/assets/brightness0.png'
import brightnessIcon1 from '@/assets/brightness1.png'
import brightnessIcon2 from '@/assets/brightness2.png'

export default {
  props: {
    showGrid: {
      type: Boolean,
      default: false,
    },
    showCustomGrid: {
      type: Boolean,
      default: null,
    },
    showBlockInfo: {
      type: Boolean,
      default: null,
    },
  },
  data() {
    return {
      mode: null,
      brightnesses: [],
    }
  },
  watch: {
    cellSize() {
      this.setBlockInfo()
      this.draw()
    },
    originX() { this.draw() },
    originY() { this.draw() },
    cursorX() { this.draw() },
    cursorY() { this.draw() },
    imageBitmap() { this.draw() },
    showGrid() { this.draw() },
    showCustomGrid() { this.draw() },
    showBlockInfo() { this.draw() },
    customGridSize() { this.draw() },
  },
  computed: {
    cellSize() {
      return this.$store.state.drawCellSize
    },
    originX() {
      return this.$store.state.drawOriginX
    },
    originY() {
      return this.$store.state.drawOriginY
    },
    cursorX() {
      return this.$store.state.cursorX
    },
    cursorY() {
      return this.$store.state.cursorY
    },
    imageWidth() {
      return this.$store.getters.imageWidth
    },
    imageHeight() {
      return this.$store.getters.imageHeight
    },
    imageData() {
      if(!this.$store.state.image.imageData) { return }
      return this.$store.state.image.imageData
    },
    imageBitmap() {
      if(!this.$store.state.image.imageData) { return }
      return this.$store.state.image.imageBitmap
    },
    canvasWidth() {
      return this.$vuetify.breakpoint.width
    },
    canvasHeight() {
      return this.$vuetify.breakpoint.height - 140
    },
    customGridSize() {
      return this.$store.state.config.customGridSize
    },
  },
  methods: {
    draw() {
      if(!this.imageBitmap) { return }

      const ctx = this.ctx
      ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight)

      // image
      const cSize = this.cellSize
      ctx.drawImage(this.imageBitmap, this.originX, this.originY, this.imageWidth * this.cellSize, this.imageHeight * this.cellSize)

      // block info
      if(this.showBlockInfo && this.cellSize > 45) {
        ctx.font = `${Math.round(cSize / 5)}px sanserif`
        for (let y = Math.max(-Math.floor(this.originY / cSize) - 1, 0); y < this.imageHeight; y++) {
          for (let x = Math.max(-Math.floor(this.originX / cSize) - 1, 0); x < this.imageWidth; x++) {
            const posX = x * cSize + this.originX
            const posY = y * cSize + this.originY
            if (posX > this.canvasWidth || posY > this.canvasHeight) { break }

            const i = (x + y * this.imageWidth) * 4
            const [r, g, b] = this.$store.state.image.imageData.data.slice(i, i + 3)
            const block = Color.getBlock(r, g, b)
            ctx.fillText(block.blockId, posX + cSize / 6, posY + cSize / 3)

            const brightness = this.brightnesses[block.brightnessIdx]
            ctx.drawImage(brightness.icon, posX + brightness.x, posY + brightness.y, brightness.width, brightness.height)
          }
        }
      }

      // grid
      if(this.showGrid && this.cellSize > 15) {
        ctx.strokeStyle = "gray"
        ctx.lineWidth = 1
        ctx.beginPath()
        ctx.setLineDash([this.cellSize / 5, this.cellSize / 5])
        for (let x = Math.max(-Math.floor(this.originX / cSize), 0); x <= this.imageWidth; x++) {
          const posX = x * cSize + this.originX
          if(posX > this.canvasWidth) { break }

          ctx.moveTo(posX, this.originY)
          ctx.lineTo(posX, this.imageHeight * cSize + this.originY)
        }
        for (let y = Math.max(-Math.floor(this.originY / cSize), 0); y <= this.imageHeight; y++) {
          const posY = y * cSize + this.originY
          if(posY > this.canvasHeight) { break }

          ctx.moveTo(this.originX, posY)
          ctx.lineTo(this.imageWidth * cSize + this.originX, posY)
        }
        ctx.stroke()
      }

      //rough-grid
      if(this.showCustomGrid) {
        ctx.strokeStyle = "cyan"
        ctx.lineWidth = 1
        ctx.beginPath()
        ctx.setLineDash([this.cellSize / 5, this.cellSize / 5])
        for (let x = 0; x <= this.imageWidth; x += this.customGridSize) {
          const posX = x * cSize + this.originX
          if(posX > this.canvasWidth) { break }

          ctx.moveTo(posX, this.originY)
          ctx.lineTo(posX, this.imageHeight * cSize + this.originY)
        }
        for (let y = 0; y <= this.imageHeight; y += this.customGridSize) {
          const posY = y * cSize + this.originY
          if(posY > this.canvasHeight) { break }

          ctx.moveTo(this.originX, posY)
          ctx.lineTo(this.imageWidth * cSize + this.originX, posY)
        }
        ctx.stroke()
      }

      // cursor
      if(this.cursorX !== null && this.cursorY !== null) {
        ctx.strokeStyle = "black"
        ctx.lineWidth = 1
        ctx.beginPath()
        ctx.setLineDash([])
        ctx.moveTo((this.cursorX + 1) * cSize + this.originX, this.originY)
        ctx.lineTo((this.cursorX + 1) * cSize + this.originX, this.imageHeight * cSize + this.originY)
        ctx.moveTo(this.originX, (this.cursorY + 1) * cSize + this.originY)
        ctx.lineTo(this.imageWidth * cSize + this.originX, (this.cursorY + 1) * cSize + this.originY)
        ctx.stroke()
      }
    },
    mouseDown(e) {
      if(e.button === 0) { // left click
        if(this.$store.state.spaceKey) {
          this.mode = "moveField"
        } else {
          switch (this.$store.state.cursorMode) {
            case "move":
              this.mode = "moveField"
              break
            case "pencil":
              this.mode = "draw"
              this.setCursor(e.layerX, e.layerY)
              this.$store.commit("setColor", {x: this.cursorX, y: this.cursorY})
              this.$store.dispatch("calcImageBitmap")
              break
            case "fill":
              this.setCursor(e.layerX, e.layerY)
              this.$store.commit("fillColor", {x: this.cursorX, y: this.cursorY})
              this.$store.dispatch("calcImageBitmap")
          }
        }
      } else if (e.button === 2) { // right click
        const i = (this.cursorX + this.cursorY * this.imageWidth) * 4
        const [r, g, b] = this.$store.state.image.imageData.data.slice(i, i + 3)
        const color = Color.getBlock(r, g, b)
        this.$store.commit("setCurrentColor", {blockId: color.blockId, brightnessIdx: color.brightnessIdx})
      }
    },
    mouseMove(e) {
      const oldX = this.cursorX
      const oldY = this.cursorY
      switch (this.mode) {
        case "moveField":
          this.$store.commit("setDrawOriginX", this.originX + e.movementX)
          this.$store.commit("setDrawOriginY", this.originY + e.movementY)
          break
        case "draw":
          this.setCursor(e.layerX, e.layerY)
          this.$store.commit("lineColor", {x1: oldX, y1: oldY, x2: this.cursorX, y2: this.cursorY})
          this.$store.dispatch("calcImageBitmap")
          break
        default:
          this.setCursor(e.layerX, e.layerY)
      }
    },
    mouseUp() {
      this.mode = null
    },
    mouseWheel(e) {
      if(!(this.$store.state.spaceKey || this.$store.state.cursorMode === "move")) { return }

      const oldCursorX = (e.layerX - this.originX) / this.cellSize
      const oldCursorY = (e.layerY - this.originY) /this.cellSize

      if(e.deltaY < 0) {
        this.$store.commit("setDrawCellSize", Math.min(this.cellSize * Math.SQRT2, 128))
      } else {
        this.$store.commit("setDrawCellSize", Math.max(this.cellSize / Math.SQRT2, 1))
      }

      this.$store.commit("setDrawOriginX", e.layerX - oldCursorX * this.cellSize)
      this.$store.commit("setDrawOriginY", e.layerY - oldCursorY * this.cellSize)
    },
    setCursor(layerX, layerY) {
      let cursorX = Math.floor((layerX - this.originX) / this.cellSize)
      if(cursorX < 0 || cursorX >= this.imageWidth) { cursorX = null }
      this.$store.commit("setCursorX", cursorX)

      let cursorY = Math.floor((layerY - this.originY) / this.cellSize)
      if(cursorY < 0 || cursorY >= this.imageHeight) { cursorY = null }
      this.$store.commit("setCursorY", cursorY)
    },
    setBlockInfo() {
      this.brightnesses.forEach((brightness) => {
        const [w, h] = [brightness.icon.width, brightness.icon.height]
        if(w > h) {
          brightness.width = this.cellSize / 4
          brightness.height  = this.cellSize / 4 * h / w
        } else {
          brightness.width = this.cellSize / 4 * w / h
          brightness.height  = this.cellSize / 4
        }

        brightness.x = this.cellSize / 2 + (this.cellSize / 2 - brightness.width) / 2
        brightness.y = (this.cellSize / 2 - brightness.height) / 2
      })
    },
  },
  created() {
    const icons = [...new Array(3)].map(() => new Image())
    icons[0].src = brightnessIcon0
    icons[1].src = brightnessIcon1
    icons[2].src = brightnessIcon2
    icons.forEach((icon, i) => {
      this.brightnesses[i] = {
        icon: icon,
        width: 0, height: 0,
        x: 0, y: 0,
      }
    })
  },
  mounted() {
    this.ctx = this.$el.getContext('2d')
    this.ctx.imageSmoothingEnabled = false

    this.$el.oncontextmenu = function() { return false }
  },
  updated() {
    this.ctx.imageSmoothingEnabled = false
    this.draw()
  },
}
</script>

<style scoped>
</style>
